




































import { Component, Vue } from 'vue-property-decorator';
import UserItem from '@/components/usersearch/UserItem.vue';

@Component({
  components: {
    UserItem,
  },
})

export default class UserSearch extends Vue {
  private searchValue?: string = '';
  private isLoading?: boolean = false;
  private loading?: boolean = false;
  private finished?: boolean = false;
  private onSearch(val: any) {
    console.log(val);
  }
  private goBackPage() {
    this.$router.back();
  }
  private onLoad(): void {
    setTimeout(() => {
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
  private onRefresh () {
    setTimeout(() => {
      this.isLoading = false;
    }, 1000);
  }
}
